.text {
  display: flex;
  justify-content: center;
  align-items: center;
  align-content: space-around;
  flex-direction: column;
  font-size: 50px;
  height: 800px;
}
.icon-left {
  position: fixed;
  top: 60px;
  left: 60px;
  font-size: 200px;
}
.icon-right {
  position: fixed;
  bottom: 30px;
  right: 60px;
  font-size: 200px;
}
.Winter {
  color: blue;
  background-color: lightblue;
}
.Summer {
  color: red;
  background-color: cornsilk;
}
